.twoThousandGame {
  text-align: center;
}
.THGameGrid {
  margin: 0 auto;
  display: grid;
  width: 510px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-gap: 10px;
  grid-auto-flow: row dense;
  justify-content: center;
  align-items: center;
}
.THGameGridItem {
  width: 120px;
  height: 120px;
  border: 1px solid black;
  font-size: 3em;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}
