.navBar {
  position: relative;
  width: 100vw;
  height: 50px;
  box-sizing: border-box;
  padding: 3px;
  background-color: lightslategray;
}
.logoInNav {
  height: 100%;
  position: absolute;
  left: 0;
  
}

.link {
  text-decoration: none;
  text-align: center;
  padding: 0 20px;
  color: white;
  font-weight: 600;
  transition: all 300ms;
  line-height: 25px;
}
.home {
  position: absolute;
  left: 5%;
  top: calc(50% - 12.5px)
}

.HomeUsers {
  position: relative;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 16px;
  margin: 0 auto;
  width: 80%;
  height: calc(100vh - 50px);
}

.HomeUsers img {
  width: 60px;
  height: 60px;
}

.catalog {
  position: absolute;
  left: 12%;
  top: calc(50% - 12.5px)
}

.link:hover {
  color: black;
  font-weight: 800;
  
}

.link::after {
  content: "";
  display: block;
  transform: scaleX(0);
  transform-origin: 50%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 300ms;
}

.link:hover::after {
  transform: scaleX(100%);
}

