.wordle {
  width: 100vw;
  height: 100vh;
  text-align: center;
}
.wordle-board {
  width: 540px;
  height: 60%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 16px;
  margin: 0 auto;
  align-items: center;
}
.wordleInput {
  margin: 0 auto;
  width: 100px;
  height: 100px;
  border: 1px solid black;
  font-size: 4em;
  text-align: center;
}
.correct {
  background-color: green;
}
.wrongPlaced {
  background-color: yellow;
}
.wrong {
  background-color: red;
}
.cheatMode {
  position: absolute;
  right: 0;
  top: 70px;
}
.isRusCheckBox {
  display: flex   ;
  position: absolute;
  bottom: 0;
  left: 0;
}
