.data {
  position: relative;
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
}
.data img {
  width: 100%;
}
.container {
  position: absolute;
  display: inline;
  width: 40%;
  border: 5px solid orange;
  background-color: white;
}
.right {
  right: 0;
  bottom: 0;
  z-index: 3;
}
.middle {
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}
.left {
  left: 0;
  bottom: 0;
  z-index: 1;
}
main {
  width: 100vw;
  height: calc(100vh - 50px);
  box-sizing: border-box;
  background-repeat: none;
  background-position: center;
  background-size: cover;
  background-image: url(https://c4.wallpaperflare.com/wallpaper/829/249/298/minimalism-4k-hq-desktop-download-wallpaper-preview.jpg);
}
main {
  display: flex;
  align-items: center;
}
main h1 {
  margin: 0 auto;
  color: antiquewhite;
  font: 600;
  font-size: 4em;
}
