.inputbox {
  position: relative;
  width: 196px;
  display: inline-block;
  margin: 0 30px;
}
.Randmize {
  width: 80%;
  margin: 50px auto;
  display: flex;
  vertical-align: top;
}
.logsRandomizer {
  height: 100vh;
  width: 400px;
  margin: 0 50px;
  display: inline-block;
  overflow: hidden;
}
.RandomizeMain {
  display: inline-block;
  margin: 0;
  border: #23242a solid 1px;
  border-radius: 20px;
  padding: 20px;
  text-align: center;
  height: fit-content;
}
.lastResult {
  display: block;
  color: greenyellow;
  font-size: 5em;
  font-weight: 600;
}
.inputbox input {
  position: relative;
  width: 100%;
  padding: 20px 10px 10px;
  background: transparent;
  outline: none;
  box-shadow: none;
  border: none;
  color: #23242a;
  font-size: 1em;
  letter-spacing: 0.05em;
  transition: 0.5s;
  z-index: 10;
}
.inputbox span {
  position: absolute;
  left: 0;
  padding: 20px 10px 10px;
  font-size: 1em;
  color: #8f8f8f;
  letter-spacing: 0.05em;
  transition: 0.5s;
  pointer-events: none;
  transform: translateY(-46px);
}
.inputbox input:valid ~ span,
.inputbox input:focus ~ span {
  color: #45f3ff;
  transform: translateX(-10px) translateY(-84px);
  font-size: 0,75em;
}
.inputbox i {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #45f3ff;
  border-radius: 4px;
  transition: 0.5s;
  pointer-events: none;
  z-index: 9;
}
.inputbox input:valid ~ i,
.inputbox input:focus ~ i {
  height: 44px;
}
.RandmizeBtn {
  margin-top: 100px;
}
