.TST-input {
  width: 100vw;
  height: 100px;
  border: 0;
  text-align: center;
  font-size: 3em;
  color: gray;
  overflow: hidden;
}
.TST-input:focus {
  outline: none;
}
.TST-header {
  width: 100vw;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #5dc0fd;
}
.TST-article {
  width: 100vw;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: lightskyblue;
}
.TST-text {
  width: 80%;
  height: 70%;
  text-align: center;
  text-align: justify;
  padding: 20px;
  box-sizing: border-box;
  border: 2px solid gray;
  border-radius: 20px;
  text-overflow: initial;
  font-size: 2em;
  color: gray;
  background-color: white;
}
.TST-footer {
  width: 100vw;
  height: calc(100vh - 60vh - 252px);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: lightskyblue;
}
.btnTypeSpeed {
  font-size: large;
  border: 1px solid black;
  border-radius: 20px;
  width: 120px;
  height: 70px;
  font-weight: 700;
  background-color: white;
  color: black;
}
