.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  height: fit-content;
  margin: 0 auto;
  width: 80vw;
  justify-content: center;
  text-align: center;
}
.cards .card {
  margin: 30px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  height: fit-content;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  transition: 400ms;
}
.cards .card p.tip {
  font-size: 1em;
  font-weight: 700;
  color: #353331;
}
.cards .card p.second-text {
  font-size: 0.7em;
  color: #b1a08b;
}
.cards .card:hover {
  transform: scale(1.1, 1.1);
}
.cards .red {
  background-color: red;
}
.cards .white {
  border: 1px solid #665e5e;
  color: #b1a08b;
}
.card:not(:hover) {
  transform: scale(0.9, 0.9);
  color: #b1a08b;
}
.link {
  margin: 0;
  padding: 0;
}
